import React from 'react';

function Card (launch) {
    const{mission_id, launch_year,launch_success, mission_name,flight_number} = launch;
    const {mission_patch_small, article_link} = launch.links
    const {land_success} = launch.rocket.first_stage.cores[0]
    return(
        <article className="shadow card">
               
                <img src={mission_patch_small} alt={mission_name}/>
                    <a href={article_link} target="_blank" rel="noreferrer" className="link holder">{mission_name}#{flight_number}</a>
                        <div className="grid block">Mission iDs: <span>{mission_id}</span></div> 
                        <div className="grid block">Launch Year: <span>{launch_year}</span></div> 
                        <div className="grid block">Successful Launch: <span>{launch_success ? 'true' : 'false'}</span></div> 
                        <div className="grid block">Successful Landing: <span>{land_success ? 'true' : 'false'}</span></div> 
                    <ul>
                    {/* <li>
                        <strong>Launch Year:</strong> 
                        <span> {launch_year}</span>
                    </li>
                    <li>
                        <strong>Successful Launch:</strong>
                        <span> {launch_success ? 'true' : 'false'}</span>
                    </li>
                    <li>
                        <strong>Successful Landing:</strong> 
                        <span> {land_success ? 'true' : 'false'}</span>
                    </li> */}
                </ul>
            </article>
    )
}

export default Card